import React from "react"
import { Box } from "@chakra-ui/react"

import { chakraComponents, AsyncSelect } from "chakra-react-select"

import { graphql, navigate, useStaticQuery } from "gatsby"
import { useFlexSearch } from "react-use-flexsearch"
import useApiVersioning from "../../../hooks/useApiVersioning"
import { ChevronRightIcon, Search2Icon } from "@chakra-ui/icons"

const customComponents = {
  Control: ({ children, ...props }) => (
    <chakraComponents.Control {...props}>
      <Search2Icon boxSize={6} pl="0.5em" />
      {children}
    </chakraComponents.Control>
  ),
  IndicatorSeparator: () => null,
  DropdownIndicator: props => (
    <chakraComponents.DropdownIndicator {...props}>
      <ChevronRightIcon boxSize={7} />
    </chakraComponents.DropdownIndicator>
  ),
}

function SearchInput() {
  const [searchQuery, setSearchQuery] = React.useState("")
  const { selectedApiVersion } = useApiVersioning()

  const queryData = useStaticQuery(graphql`
    query {
      localSearchPages {
        index
        store
      }
    }
  `)

  const index = queryData.localSearchPages.index
  const store = queryData.localSearchPages.store

  const searchResults = useFlexSearch(searchQuery, index, store)

  const navigateToSearchResult = async path => {
    try {
      await navigate(`/${path}`)
    } catch (e) {
      console.error(e)
    }
  }

  const filterSearchResults = searchResults => {
    return searchResults.filter(
      result =>
        result.version === selectedApiVersion.name &&
        result.title !== null &&
        result.title !== "" &&
        result.path !== null &&
        !result.path.toLowerCase().endsWith("/toc") &&
        result.path.toLowerCase() !== "toc"
    )
  }

  const filteredSearchResults = () =>
    new Promise(resolve => {
      resolve(
        filterSearchResults(searchResults).map(result => ({
          ...result,
          label: result.title,
          value: result.id,
        }))
      )
    })

  const formatOptionLabel = ({ title, description }) => (
    <Box cursor="pointer" px={1} rounded="sm">
      <Box fontWeight="semibold" lineHeight="tight" noOfLines={1}>
        {title}
      </Box>
      {description && (
        <Box color="gray.600" fontSize="sm" noOfLines={1}>
          {description}
        </Box>
      )}
    </Box>
  )

  return (
    <Box>
      <AsyncSelect
        controlShouldRenderValue={false}
        placeholder="Search our documentation"
        noOptionsMessage={() => "No results found"}
        onChange={selectedOption => {
          navigateToSearchResult(selectedOption.path)
        }}
        formatOptionLabel={formatOptionLabel}
        components={customComponents}
        loadOptions={filteredSearchResults}
        onInputChange={input => {
          setSearchQuery(input)
        }}
        chakraStyles={{
          control: styles => ({
            ...styles,
            background: "white",
            textAlign: "left",
          }),
          dropdownIndicator: styles => ({ ...styles, background: "white" }),
          noOptionsMessage: styles => ({
            ...styles,
            textAlign: "left",
            paddingLeft: "1em",
          }),
          option: (styles, state) => ({
            ...styles,
            backgroundColor: state.isSelected
              ? "rlYellow"
              : state.isFocused
              ? "rlYellow40"
              : null,
          }),
          menuList: styles => ({ ...styles, padding: "0.75em" }),
        }}
      />
    </Box>
  )
}

export default SearchInput
