import React from "react"
import { Stack } from "@chakra-ui/react"
import MobileSubNav from "./mobileSubNav/mobileSubNav"

const MobileNav = ({ documentationData }) => {
  return (
    <Stack
      bg="rlBlack20"
      p={4}
      display={{ md: "none" }}
      data-testid="mobile-title"
    >
      {documentationData
        .filter(navItem => navItem.children.length > 1)
        .map(navItem => (
          <MobileSubNav key={navItem.textElement} {...navItem} />
        ))}
    </Stack>
  )
}

export default MobileNav
