import React from "react"
import {
  Box,
  Flex,
  Link,
  Popover,
  PopoverTrigger,
  PopoverContent,
  useDisclosure,
} from "@chakra-ui/react"
import { TriangleDownIcon } from "@chakra-ui/icons"

const Dropdown = ({ children, title }) => {
  const { isOpen, onToggle } = useDisclosure()
  return (
    <Box
      _last={{
        rounded: "0.6rem",
        borderWidth: "0.18rem",
        borderColor: "rlYellow",
        px: "0.37rem",
      }}
      onClick={onToggle}
    >
      <Popover trigger={"hover"} placement={"bottom-start"}>
        <PopoverTrigger>
          <Link
            href={"#"}
            fontSize={"xl"}
            fontWeight={500}
            color={"rlWhite"}
            _hover={{
              textDecoration: "none",
              color: "rlYellow",
            }}
          >
            <Flex align={"center"} gap={3} data-testid="title" fontSize={24}>
              {title}
              <TriangleDownIcon
                boxSize={3}
                color={"rlWhite"}
                transition={"all .25s ease-in-out"}
                transform={isOpen ? "rotate(180deg)" : ""}
              />
            </Flex>
          </Link>
        </PopoverTrigger>
        <PopoverContent
          boxShadow={"xl"}
          borderRadius={0}
          bg={"rlWhite"}
          p={1}
          style={{ marginTop: "0!important" }}
        >
          {children}
        </PopoverContent>
      </Popover>
    </Box>
  )
}

export default Dropdown
