import React from "react"
import { Stack, Text } from "@chakra-ui/react"
import { Link as GatsbyLink } from "gatsby"

const DesktopSubNav = ({ textElement, href }) => {
  return (
    <GatsbyLink
      to={href}
      style={{
        role: "group",
        display: "block",
      }}
    >
      <Stack
        direction={"row"}
        align={"center"}
        _hover={{ bg: "rlYellow" }}
        p={1}
      >
        <Text
          transition={"all .3s ease"}
          fontWeight={600}
          style={{ textTransform: "capitalize", _hover: { bg: "rlYellow" } }}
        >
          {textElement}
        </Text>
      </Stack>
    </GatsbyLink>
  )
}
export default DesktopSubNav
