import {
  Box,
  Flex,
  Stack,
  Popover,
  PopoverTrigger,
  PopoverContent,
  useDisclosure,
  Link as ChakraLink,
} from "@chakra-ui/react"
import { Link as GatsbyLink } from "gatsby"
import { TriangleDownIcon } from "@chakra-ui/icons"
import React from "react"
import DesktopSubNav from "./desktopSubNav/desktopSubNav"
import Dropdown from "../../dropdown/dropdown"
import useApiVersioning from "../../../hooks/useApiVersioning"

const DesktopNav = ({ documentationData }) => {
  const { isOpen, onToggle } = useDisclosure()
  const { selectedApiVersion, apiVersions, changeSelectedApiVersion } =
    useApiVersioning()

  return (
    <Stack direction={"row"} spacing={8} alignItems={"center"}>
      {documentationData.map(navItem => (
        <Flex key={navItem.textElement} onClick={onToggle}>
          <Popover trigger="hover" placement="bottom-start">
            <PopoverTrigger>
              <GatsbyLink
                to={navItem.href ?? "#"}
                style={{
                  p: 1,
                  fontSize: "xl",
                  fontWeight: 500,
                  color: "white",
                }}
              >
                <Flex
                  _hover={{
                    color: "rlYellow",
                  }}
                  alignItems="center"
                  data-testid="dropdown-title"
                  fontSize={24}
                >
                  {navItem.textElement}
                  <TriangleDownIcon
                    boxSize={3}
                    color="white"
                    ml={3}
                    transition={"all .25s ease-in-out"}
                    transform={isOpen ? "rotate(180deg)" : ""}
                  />
                </Flex>
              </GatsbyLink>
            </PopoverTrigger>
            <PopoverContent
              boxShadow="xl"
              borderRadius={0}
              bg="white"
              p={1}
              style={{ marginTop: "0!important" }}
            >
              <Stack m={2}>
                {navItem.children.map(child => (
                  <DesktopSubNav key={child.textElement} {...child} />
                ))}
              </Stack>
            </PopoverContent>
          </Popover>
        </Flex>
      ))}

      {/*versions dropdown*/}
      <Dropdown as={Box} title={selectedApiVersion?.displayName}>
        <Stack m={2}>
          {apiVersions?.map(version => (
            <Box
              textAlign="left"
              transition={"all .3s ease"}
              fontWeight={600}
              onClick={() => {
                changeSelectedApiVersion(version)
              }}
              key={version.name}
              display="block"
              p={1}
              _hover={{ bg: "rlYellow" }}
              cursor="pointer"
            >
              {version.displayName}
            </Box>
          ))}
        </Stack>
      </Dropdown>

      <ChakraLink
        href="http://support.radiantlogic.com"
        style={{
          p: 1,
          fontSize: 24,
          color: "white",
          fontWeight: 500,
          textDecoration: "underline",
        }}
      >
        Support
      </ChakraLink>
    </Stack>
  )
}
export default DesktopNav
