import {
  Flex,
  Text,
  Stack,
  Collapse,
  Icon,
  useDisclosure,
} from "@chakra-ui/react"
import { Link as GatsbyLink } from "gatsby"
import { ChevronDownIcon } from "@chakra-ui/icons"
import React from "react"

const MobileSubNav = ({ children, href, textElement }) => {
  const { isOpen, onToggle } = useDisclosure()

  return (
    <Stack spacing={4} onClick={children && onToggle}>
      <Flex
        py={2}
        as={GatsbyLink}
        to={href ?? "#"}
        justify={"space-between"}
        align={"center"}
      >
        <Text fontWeight={600} color={"rlBlack"}>
          {textElement}
        </Text>
        {children && (
          <Icon
            as={ChevronDownIcon}
            transition={"all .25s ease-in-out"}
            transform={isOpen ? "rotate(180deg)" : ""}
            w={6}
            h={6}
          />
        )}
      </Flex>

      <Collapse in={isOpen} animateOpacity style={{ marginTop: "0!important" }}>
        <Stack
          mt={1}
          pl={4}
          borderLeft={5}
          borderStyle={"solid"}
          borderColor={"rlYellow"}
          align={"start"}
        >
          {children &&
            children.map(child => (
              <Stack key={child.textElement} _hover={{ bg: "rlYellow" }}>
                <GatsbyLink
                  to={child.href}
                  style={{
                    textTransform: "capitalize",
                    py: 2,
                  }}
                >
                  <Text transition={"all .3s ease"} fontWeight={600} p={1}>
                    {child.textElement}
                  </Text>
                </GatsbyLink>
              </Stack>
            ))}
        </Stack>
      </Collapse>
    </Stack>
  )
}

export default MobileSubNav
