import * as React from "react"
import {
  Box,
  Flex,
  IconButton,
  Stack,
  Collapse,
  useDisclosure,
} from "@chakra-ui/react"
import { HamburgerIcon, CloseIcon } from "@chakra-ui/icons"
import Logo from "../logo"
import { Link as GatsbyLink } from "gatsby"
import DesktopNav from "./desktopNav/desktopNav"
import MobileNav from "./mobileNav/mobileNav"

import SearchInput from "./searchInput/searchInput"
import useApiVersioning from "../../hooks/useApiVersioning"
import { getTocData } from "../../utils/get-toc-data"

const Header = ({ ...props }) => {
  const { isOpen, onToggle } = useDisclosure()
  const { getPathWithVersionPrefix } = useApiVersioning()

  let rootToc = null
  try {
    rootToc = getTocData(props.tocContents.htmlAst).map(elem => ({
      ...elem,
      href: getPathWithVersionPrefix(elem.href),
    }))
  } catch (e) {
    console.error(`Homepages table of contents generation failed`)
  }

  const documentationData = [
    {
      textElement: "Get Started",
      children: rootToc ?? [],
    },
  ]

  return (
    <Box>
      <Box bg="rlBlack" justifyContent="space-between" align="center">
        <Flex
          bg="rlBlack"
          minH={"5.25rem"}
          px={{ base: 6 }}
          align="center"
          justifyItems="center"
          alignItems="center"
          maxW="90rem"
        >
          <Flex flex={{ base: 4 }}>
            <Box
              display={{ base: "flex" }}
              mr="2rem"
              data-testid="logo"
              alignItems="center"
            >
              <GatsbyLink to={getPathWithVersionPrefix("/")}>
                <Logo />
              </GatsbyLink>
            </Box>
            <Stack
              spacing={8}
              alignItems={"left"}
              maxW={"37.5rem"}
              width={{
                base: "100%",
                md: "80%",
                lg: "70%",
                xl: "100%",
                "2xl": "100%",
              }}
            >
              <SearchInput />
            </Stack>
          </Flex>

          <Stack justify={"flex-end"} direction={"row"} spacing={6} pl={5}>
            <Flex
              display={{ base: "none", md: "flex" }}
              mr={2}
              alignItems={"center"}
            >
              <DesktopNav documentationData={documentationData} />
            </Flex>
          </Stack>

          {/*Hamburger menu that appears for small screens*/}
          {documentationData[0].children.length > 1 && (
            <Flex
              flex={{ base: 0.3, md: "auto" }}
              mr={{ base: -2 }}
              display={{ base: "flex", md: "none" }}
              pl={5}
              data-testid="menu-button"
            >
              <IconButton
                onClick={onToggle}
                icon={
                  isOpen ? (
                    <CloseIcon w={3} h={3} />
                  ) : (
                    <HamburgerIcon w={5} h={5} />
                  )
                }
                aria-label={"Toggle Navigation"}
              />
            </Flex>
          )}
        </Flex>
      </Box>
      <Collapse in={isOpen} animateOpacity>
        <MobileNav documentationData={documentationData} />
      </Collapse>
    </Box>
  )
}

export default Header
